<template>
  <v-app class="loginApp">
    <div class="app">
      <div class="loginForm">
        <v-form
          ref="form"
          v-model="valid"
          lazy-validation
        >
        <v-row>
          <v-col>
            <v-text-field
            v-model="name"
            :counter="10"
            :rules="nameRules"
            label="Name"
            required
            filled
            rounded
            dense
          ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-text-field
            v-model="number"
            :rules="numberRules"
            label="Phone number"
            required
            filled
            rounded
            dense
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col
          cols="8"
          >
            <v-text-field
            v-model="addr"
            :rules="addrRules"
            label="addr"
            required
            filled
            rounded
            dense
          ></v-text-field>
          </v-col>
          <v-col>
            <v-select
              v-model="select"
              :items="items"
              :rules="[v => !!v || 'Item is required']"
              label="sex"
              required
              filled
              rounded
              dense
            ></v-select>
          </v-col>
        </v-row>

          <v-checkbox
            v-model="checkbox"
            :rules="[v => !!v || 'You must agree to continue!']"
            label="Do you agree?"
            required
            filled
            rounded
            dense
          ></v-checkbox>

          <v-btn
            :disabled="!valid"
            color="success"
            class="mr-4"
            @click="validate"
            filled
            rounded
            dense
            type="submit"
          >
            Validate
          </v-btn>

          <v-btn
            color="error"
            class="mr-4"
            @click="reset"
            filled
            rounded
            dense
          >
            Reset Form
          </v-btn>
        </v-form>
      </div>
    </div>
  </v-app>
</template>

<script>
  export default {
    name: 'loginApp',
    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters',
      ],
      number: '',
      numberRules: [
        v => !!v || 'number is required',
        v => /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/.test(v) || 'number must be valid',
      ],
      addr: '',
      addrRules: [
        v => !!v || 'E-mail is required',
      ],
      select: null,
      items: [
        '남성',
        '여성',
        '안알랴줌',
        '외계인',
      ],
      checkbox: false,
    }),

    methods: {
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
    },
  }
</script>

